$primary-color: #d52b1e;

a {
	color: $primary-color;
	
	&:hover {
		color: $primary-color;
	}
}

p {
	line-height: 1.5rem;
}

.back-arrow {
	width: 36px;
	cursor: pointer;
}

.card {
	border-radius: 10px;
	box-shadow: 0px 2px 4px -1px rgba(0, 0, 0, 0.2), 0px 4px 5px 0px rgba(0, 0, 0, 0.14), 0px 1px 10px 0px rgba(0, 0, 0, 0.12);

	.card-img-top {
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
	}

	.card-title {
		font-size: 1.5em;
	}
}

.actions {
	.btn {
		color: #FFF;
		background-color: $primary-color;
		border-color: $primary-color;
		width: 100%;
		min-width: 18rem;
		height: 3rem;
		border-radius: 10px;
		font-size: 1.3em;
    font-weight: bold;

		&:hover {
			color: #FFF;
		}
	}
}

.btn-outline-primary {
	color: $primary-color;
	background-color: #FFF;
	border-color: $primary-color;

	&:hover {
		color: #FFF;
		background-color: $primary-color;
		border-color: $primary-color;
	}
}

ul {
	list-style: none;

	li {
		line-height: 1.5rem;
		margin: 0.3em 0px;

		&::before {
			content: "\2022";
			color: $primary-color;
			font-weight: bold;
			display: inline-block; 
			width: 0.7em;
			transform: scale(2.5);
			margin-left: -1rem;
		}
	}
}

ol {
	counter-reset: li 0;
	list-style: outside none;
	padding-left: 0.8em;

	li {
		counter-increment: li 1;
	    margin: 0.3em 0px;
	    padding-left: 1em;
		line-height: 1.5rem;

		&::before {
			content: counter(li);
			color: $primary-color;
			width: 1em;
			height: 1em;
			margin-left: -1em;
			margin-right: 0.5em;
			border: 1px solid $primary-color;
			text-align: center;
			border-radius: 50%;
			position: relative;
			bottom: 0.1em;
			font-size: 0.8em;
			padding: 0.7em;
			display: inline-grid;
			place-content: center;
			text-indent: 0px;
		}
	}
}

.sweet-alert {
	width: 42rem!important;
}